.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

.App-store {
  height: 10vmin;
  pointer-events: none;
}

.Header-image {
  height: 15vmin;
  pointer-events: none;
}

