.App-header {
  background-color: #2bbb64;
}

.App-footer {
  background-color: #2bbb64;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: white;
  align-self: stretch;
  /* width: 100%; */
}

.App-link {
  color: #61dafb;
}

.body {
  background-color: #f0f0f0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  align-self: stretch;
}

